import React, {
  Component,
  PropsWithChildren
} from "react";
import { Box, Grid, Typography } from "@mui/material";

interface FormGroupProps extends PropsWithChildren {
  heading: string;
}
export class FormGroup extends Component<FormGroupProps> {
  static Item(props: PropsWithChildren) {
    return (
      <Grid item xs={12} sm={6}>
        {props.children}
      </Grid>
    );
  }
  render() {
    return (
      <Box marginBottom={4}>
        <Typography variant="h6" color="secondary" marginBottom={1}>{this.props.heading}</Typography>
        <Grid container spacing={2}>
          {this.props.children}
        </Grid>
      </Box>
    );
  }
}
