import { V1Account } from "../../services/servicesSchemas";
import { isUndefined } from "lodash";

export function missingDetails(account?: V1Account) {
  switch (account?.role) {
    case "ROLE_ADMIN":
      return isUndefined(account.details?.admin_details);
    case "ROLE_CLIENT":
      return isUndefined(account.details?.client_details);
  }
  return true;
}
