import { Typography } from "@mui/material";
import { Link } from "react-router-dom";
import { PropsWithChildren } from "react";
import ALink from "react-anchor-link-smooth-scroll";
interface TextLinkProps extends PropsWithChildren {
  to: string;
}
export function TextLink({ children, ...props }: TextLinkProps) {
  return (
    <Link {...props} preventScrollReset={false}>
      <Typography color="secondary" display="inline">
        {children}
      </Typography>
    </Link>
  );
}

interface AnchorLinkProps extends PropsWithChildren {
  href: string;
}
export function AnchorLink({ children, ...props }: AnchorLinkProps) {
  return (
    <ALink {...props}>
      <Typography color="secondary" display="inline">
        {children}
      </Typography>
    </ALink>
  );
}
