import { useContext } from "react";
import { DashboardContext } from "../../layout/DashboardLayout";
import { useAccountsServiceUpdateAccountDetails } from "../../services/servicesComponents";
import { useNavigate } from "react-router-dom";
import { useQueryClient } from "@tanstack/react-query";
import { queryKeyFn } from "../../services/servicesContext";

export function useAccountSubmit(id: string, onFinish: () => void) {
  const navigate = useNavigate();

  const queryClient = useQueryClient();
  const { headers } = useContext(DashboardContext);

  const { mutate, isLoading } = useAccountsServiceUpdateAccountDetails({
    onSuccess: (data) => {
      navigate("/dashboard/profile/" + id);
      queryClient.setQueryData(
        queryKeyFn({
          path: "/v1/accounts/{id}",
          operationId: "accountsServiceGetAccount",
          variables: { pathParams: { id } },
        }),
        data
      );
      onFinish();
    },
  });

  return { mutate, headers, isLoading };
}
