import { Component, useContext } from "react";
import { DashboardContext } from "../../layout/DashboardLayout";
import { useAccountsServiceGetCurrentAccount } from "../../services/servicesComponents";
import { V1Account } from "../../services/servicesSchemas";
import { Box, Dialog, DialogProps } from "@mui/material";
import { useBoolean } from "../../hooks";
import { AdminDetailsForm } from "./AdminDetailsForm";
import { missingDetails } from "./missingDetails";
import { ClientDetailsForm } from "./ClientDetailsForm";
import { Loader } from "../../components/Loader";

export default function ProfileForm() {
  const [isOpen, , close] = useBoolean(true);
  const { headers } = useContext(DashboardContext);
  const { data, isLoading } = useAccountsServiceGetCurrentAccount({
    headers: { ...headers },
  });

  if (isLoading) return <Loader />;

  return (
    <DetailsForm.Dialog open={isOpen && missingDetails(data?.account)}>
      <DetailsForm account={data?.account} onFinish={close} />
    </DetailsForm.Dialog>
  );
}
export interface FormProps {
  account?: V1Account;
  onFinish(): void;
}

export class DetailsForm extends Component<FormProps> {
  static Dialog({ children, ...props }: DialogProps) {
    return (
      <Dialog maxWidth="lg" {...props}>
        <Box paddingX="5%" paddingY={5}>
          {children}
        </Box>
      </Dialog>
    );
  }

  render() {
    switch (this.props.account?.role) {
      case "ROLE_ADMIN":
        return <AdminDetailsForm {...this.props} />;

      case "ROLE_CLIENT":
        return <ClientDetailsForm {...this.props} />;

      default:
        return <>No form for role {this.props.account?.role}</>;
    }
  }
}
