import React, { useContext } from "react";
import { DashboardContext } from "../../layout/DashboardLayout";
import { useAccountsServiceListAccounts } from "../../services/servicesComponents";
import { Link } from "react-router-dom";
import { Chip, Grid } from "@mui/material";
import { useProfilesFilter, FilterActions } from "./components/ProfilesFilter";
import { Section } from "../Profile/components/Section";
import { V1Account } from "../../services/servicesSchemas";
import { Loader } from "../../components/Loader";

export default function Profiles() {
  const filters = useProfilesFilter({
    "filter.offset": "0",
    "filter.limit": "5",
  });

  const { headers } = useContext(DashboardContext);
  const { data, isLoading } = useAccountsServiceListAccounts({
    queryParams: filters.filters,
    headers,
  });

  return (
    <>
      <FilterActions {...filters} data={data} />
      <Grid
        container
        direction="column"
        justifyContent="flex-start"
        alignItems="center"
      >
        {isLoading && <Loader />}
        {data?.accounts?.map((a) => (
          <>
            <Grid item>
              <Link to={`/dashboard/profile/` + a.id}>
                <Section>
                  <Section.Item label="Nombre">
                    {roleClassifier(a)[1]}
                  </Section.Item>
                  <br />
                  <Section.Item label="Correo Electronico">
                    {a.email}
                  </Section.Item>
                  <br />
                  <Section.Item label="Rol">
                    {roleClassifier(a)[0]}
                  </Section.Item>
                </Section>
              </Link>
            </Grid>
            <br />
          </>
        ))}
      </Grid>
    </>
  );
}

function fullName(value?: { first_name?: string; last_name?: string }): string {
  if (!value) return "Sin datos";
  return `${value?.first_name} ${value?.last_name}`;
}

function roleClassifier(a: V1Account): [JSX.Element, JSX.Element] {
  switch (a.role) {
    case "ROLE_ADMIN":
      return [
        <Chip label="Administrador" />,
        <>{fullName(a.details?.admin_details)}</>,
      ];
    case "ROLE_CLIENT":
      return [
        <Chip label="Cliente" />,
        <>{fullName(a.details?.client_details)}</>,
      ];
    case "ROLE_JUDGE":
      return [<Chip label="Juez" />, <>{fullName(a.details?.judge_details)}</>];

    case "ROLE_LAWYER":
      return [
        <Chip label="Abogado" />,
        <>{fullName(a.details?.lawyer_details)}</>,
      ];

    case "ROLE_MANAGER":
      return [
        <Chip label="Administrador" />,
        <>{fullName(a.details?.manager_details)}</>,
      ];

    case "ROLE_UNSPECIFIED":
    default:
      return [
        <Chip label="No especificado" />,
        <>{fullName(a.details?.unspecified_details)}</>,
      ];
  }
}
