import { useCallback } from "react";
import { AccountAdminDetails } from "../../services/servicesSchemas";
import { Grid, InputAdornment, TextField } from "@mui/material";
import { useFormik } from "formik";
import { FormGroup } from "../../components/FormGroup";
import { Send } from "@mui/icons-material";
import BadgeIcon from "@mui/icons-material/Badge";
import { useAccountSubmit } from "./useAccountSubmit";
import { FormProps } from ".";
import { LoadingButton } from "@mui/lab";

export function AdminDetailsForm(props: FormProps) {
  const { mutate, headers, isLoading } = useAccountSubmit(
    props.account!.id!,
    props.onFinish
  );

  const handleSubmit = useCallback(
    async (admin_details: AccountAdminDetails) =>
      mutate({
        pathParams: { id: props.account!.id! },
        body: { admin_details },
        headers,
      }),
    [headers, mutate, props]
  );

  const formik = useFormik({
    initialValues: {
      ...props.account?.details?.admin_details,
    } as AccountAdminDetails,
    onSubmit: handleSubmit,
  });

  return (
    <form onSubmit={formik.handleSubmit}>
      <FormGroup heading="Datos del administrador">
        <FormGroup.Item>
          <TextField
            fullWidth
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <BadgeIcon />
                </InputAdornment>
              ),
            }}
            id="first_name"
            name="first_name"
            label="Nombre(s)"
            variant="filled"
            value={formik.values.first_name}
            onChange={formik.handleChange}
            error={
              formik.touched.first_name && Boolean(formik.errors.first_name)
            }
            helperText={formik.touched.first_name && formik.errors.first_name}
          />
        </FormGroup.Item>
        <FormGroup.Item>
          <TextField
            fullWidth
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <BadgeIcon />
                </InputAdornment>
              ),
            }}
            id="last_name"
            name="last_name"
            label="Apellidos"
            variant="filled"
            value={formik.values.last_name}
            onChange={formik.handleChange}
            error={formik.touched.last_name && Boolean(formik.errors.last_name)}
            helperText={formik.touched.last_name && formik.errors.last_name}
          />
        </FormGroup.Item>
      </FormGroup>
      <br />
      <Grid container justifyContent="center">
        <LoadingButton
          color="primary"
          variant="contained"
          type="submit"
          endIcon={<Send />}
          loading={isLoading}
        >
          Aceptar
        </LoadingButton>
      </Grid>
    </form>
  );
}
