import React, { useContext } from "react";
import {
  useAccountsServiceGetAccount,
  useAccountsServiceGetCurrentAccount,
} from "../../services/servicesComponents";
import { DashboardContext } from "../../layout/DashboardLayout";
import { Button, Grid } from "@mui/material";
import {
  AccountAdminDetails,
  AccountClientDetails,
  V1Account,
  V1Gender,
} from "../../services/servicesSchemas";
import { DetailsForm } from "../ProfileForm";
import { useBoolean } from "../../hooks";
import { V1MaritalStatus } from "../../services/servicesSchemas";
import { Link } from "react-router-dom";
import { Section } from "./components/Section";
import ListIcon from "@mui/icons-material/List";
import EditIcon from "@mui/icons-material/Edit";
import AssignmentIcon from "@mui/icons-material/Assignment";
import { Loader } from "../../components/Loader";

interface ProfileProps {
  id: string;
}

export function Profile(props: ProfileProps) {
  const { headers } = useContext(DashboardContext);
  const { data, isLoading } = useAccountsServiceGetAccount({
    pathParams: { id: props.id },
    headers,
  });

  if (isLoading) return <Loader />;

  return <ProfileDetails account={data?.account} />;
}

export function CurrentProfile() {
  const { headers } = useContext(DashboardContext);
  const { data, isLoading } = useAccountsServiceGetCurrentAccount({
    headers,
  });

  if (isLoading) return <Loader />;

  return <ProfileDetails account={data?.account} />;
}

interface ProfileDetailsProps {
  account?: V1Account;
}

function ProfileDetails(props: ProfileDetailsProps) {
  const [isEditing, openEditor, closeEditor] = useBoolean();

  return (
    <>
      <DetailsForm.Dialog open={isEditing} onClose={closeEditor}>
        <DetailsForm account={props.account} onFinish={closeEditor} />
      </DetailsForm.Dialog>
      <Grid
        container
        direction="column"
        justifyContent="flex-start"
        alignItems="center"
        marginY={5}
      >
        <Grid item>
          <Grid>
            <Grid container spacing={1}>
              <Grid item>
                <Link to="/dashboard">
                  <Button
                    size="small"
                    variant="contained"
                    startIcon={<AssignmentIcon />}
                  >
                    mis casos
                  </Button>
                </Link>
              </Grid>
              {props.account?.role === "ROLE_ADMIN" && (
                <Grid item>
                  <Link to="/dashboard/profiles">
                    <Button
                      size="small"
                      variant="contained"
                      onClick={openEditor}
                      startIcon={<ListIcon />}
                    >
                      ver lista de usuarios
                    </Button>
                  </Link>
                </Grid>
              )}

              <Grid item>
                <Button
                  size="small"
                  variant="contained"
                  onClick={openEditor}
                  startIcon={<EditIcon />}
                >
                  editar informacin
                </Button>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        <br />

        <ProfileDetailsClassifier {...props} />
      </Grid>
    </>
  );
}

function ProfileDetailsClassifier(props: ProfileDetailsProps) {
  if (props.account?.details?.admin_details)
    return (
      <AdminDetailsProfile
        account={props.account}
        details={props.account.details.admin_details}
      />
    );
  if (props.account?.details?.client_details)
    return (
      <ClientDetailsProfile
        account={props.account}
        details={props.account.details.client_details}
      />
    );

  return <></>;
}

interface DetailsProfileProps<T> {
  account: V1Account;
  details: T;
}

function AdminDetailsProfile(props: DetailsProfileProps<AccountAdminDetails>) {
  return (
    <>
      <Section>
        <Section.Item label="Nombre del Administrador">
          {props.details.first_name} {props.details.last_name}
        </Section.Item>
        <br />
        <Section.Item label="Correo Electrnico">
          {props.account.email}
        </Section.Item>
      </Section>
      <br />
    </>
  );
}

function ClientDetailsProfile(
  props: DetailsProfileProps<AccountClientDetails>
) {
  return (
    <>
      <Section>
        <Section.Item label="Nombre del Cliente">
          {props.details.first_name} {props.details.last_name}
        </Section.Item>
        <br />
        <Section.Item label="Correo Electrnico">
          {props.account.email}
        </Section.Item>
        <br />
        <Section.Item label="Telfono">
          {props.details.phone_number}
        </Section.Item>
        <br />
        <Section.Item label="Sexo">
          {genderClassifier(props.details.gender)}
        </Section.Item>
      </Section>
      <br />
      <Section>
        <Section.Item label="Ocupacin">
          {props.details.occupation}
        </Section.Item>
        <br />
        <Section.Item label="Estado Civil">
          {maritalStatusClassifier(props.details.marital_status)}
        </Section.Item>
        <br />
        <Section.Item label="Isapres">{props.details.isapres}</Section.Item>
        <br />
        <Section.Item label="Rut">{props.details.rut}</Section.Item>
      </Section>
      <br />
      <Section>
        <Section.Item label="Direccin Particular">
          {props.details.street} {props.details.street_number}{" "}
          {props.details.apartment_number}
        </Section.Item>
        <br />
        <Section.Item label="Regin">
          {props.details.commune}, {props.details.region}
        </Section.Item>
        <br />
        <Section.Item label="Pas">Chile</Section.Item>
      </Section>
      <br />
    </>
  );
}

function maritalStatusClassifier(s?: V1MaritalStatus): string {
  switch (s) {
    case "MARITAL_STATUS_DIVORCED":
      return "Divorciado";
    case "MARITAL_STATUS_MARRIED":
      return "Casado";
    case "MARITAL_STATUS_SEPARATED":
      return "Separado";
    case "MARITAL_STATUS_SINGLE":
      return "Soltero";
    case "MARITAL_STATUS_WIDOWED":
      return "viudo";
    case "MARITAL_STATUS_UNSPECIFIED":
    default:
      return "No especificado";
  }
}

function genderClassifier(g?: V1Gender): string {
  switch (g) {
    case "GENDER_FEMALE":
      return "Femenino";
    case "GENDER_MALE":
      return "Masculino";
    case "GENDER_UNSPECIFIED":
    default:
      return "No especificado";
  }
}
