import React, { useCallback, useState } from "react";
import { AccountsServiceListAccountsQueryParams } from "../../../services/servicesComponents";
import { Grid, IconButton, TextField } from "@mui/material";
import { V1ListAccountsResponse } from "../../../services/servicesSchemas";
import ArrowBackIosNewIcon from "@mui/icons-material/ArrowBackIosNew";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import SearchIcon from "@mui/icons-material/Search";

export function useProfilesFilter(
  init?: AccountsServiceListAccountsQueryParams
): FilterActionsProps {
  const [filters, setFilters] = useState(init);
  return { filters, setFilters };
}
interface FilterActionsProps {
  filters: AccountsServiceListAccountsQueryParams | undefined;
  setFilters: React.Dispatch<
    React.SetStateAction<AccountsServiceListAccountsQueryParams | undefined>
  >;
}
interface FilteredResultProps {
  data: V1ListAccountsResponse | undefined;
}
export function FilterActions(props: FilterActionsProps & FilteredResultProps) {
  const increment = useCallback(
    (arg0?: string, arg1?: string): string | undefined => {
      if (arg0 && arg1) return (parseInt(arg0) + parseInt(arg1)).toString();
    },
    []
  );

  const decrement = useCallback(
    (arg0?: string, arg1?: string): string | undefined => {
      if (arg0 && arg1) return (parseInt(arg0) - parseInt(arg1)).toString();
    },
    []
  );

  const isZero = useCallback((arg0?: string): boolean | undefined => {
    if (arg0) return parseInt(arg0) === 0;
  }, []);

  const isLess = useCallback(
    (
      length: number | undefined,
      arg1: string | undefined
    ): boolean | undefined => {
      if (length !== undefined && arg1) return length < parseInt(arg1);
    },
    []
  );

  return (
    <>
      <Grid
        container
        direction="row"
        justifyContent="center"
        alignItems="flex-start"
        spacing={2}
        padding={5}
      >
        <Grid item>
          <TextField
            InputProps={{
              startAdornment: <SearchIcon />,
              sx: { width: 300 },
            }}
            placeholder="Buscar"
            variant="standard"
            value={props.filters?.["filter.email"]}
            onChange={(e) =>
              props.setFilters((f) => ({
                ...f,
                "filter.email": e.target.value,
              }))
            }
          />
        </Grid>
        <Grid item>
          <IconButton
            disabled={isZero(props.filters?.["filter.offset"])}
            onClick={() =>
              props.setFilters((f) => ({
                ...f,
                "filter.offset": decrement(
                  f?.["filter.offset"],
                  f?.["filter.limit"]
                ),
              }))
            }
          >
            <ArrowBackIosNewIcon />
          </IconButton>
        </Grid>
        <Grid item>
          <IconButton
            disabled={isLess(
              props.data?.accounts?.length,
              props.filters?.["filter.limit"]
            )}
            onClick={() =>
              props.setFilters((f) => ({
                ...f,
                "filter.offset": increment(
                  f?.["filter.offset"],
                  f?.["filter.limit"]
                ),
              }))
            }
          >
            <ArrowForwardIosIcon />
          </IconButton>
        </Grid>
      </Grid>
    </>
  );
}
