import { useCallback } from "react";
import { AccountClientDetails } from "../../services/servicesSchemas";
import {
  Checkbox,
  FormControl,
  FormControlLabel,
  Grid,
  InputAdornment,
  InputLabel,
  MenuItem,
  Select,
  TextField,
  Typography,
} from "@mui/material";
import { useFormik } from "formik";
import { regionCommune } from "../../data";
import { FormGroup } from "../../components/FormGroup";
import { Send } from "@mui/icons-material";
import HealthAndSafetyIcon from "@mui/icons-material/HealthAndSafety";
import BadgeIcon from "@mui/icons-material/Badge";
import WcIcon from "@mui/icons-material/Wc";
import FamilyRestroomIcon from "@mui/icons-material/FamilyRestroom";
import WorkIcon from "@mui/icons-material/Work";
import HomeIcon from "@mui/icons-material/Home";
import NumbersIcon from "@mui/icons-material/Numbers";
import ApartmentIcon from "@mui/icons-material/Apartment";
import PlaceIcon from "@mui/icons-material/Place";
import SouthAmericaIcon from "@mui/icons-material/SouthAmerica";
import PhoneIcon from "@mui/icons-material/Phone";
import { useAccountSubmit } from "./useAccountSubmit";
import { FormProps } from ".";
import { LoadingButton } from "@mui/lab";

export function ClientDetailsForm(props: FormProps) {
  const { mutate, headers, isLoading } = useAccountSubmit(
    props.account!.id!,
    props.onFinish
  );

  const handleSubmit = useCallback(
    async (client_details: AccountClientDetails) =>
      mutate({
        pathParams: { id: props.account!.id! },
        body: { client_details },
        headers,
      }),
    [headers, mutate, props]
  );

  const formik = useFormik({
    initialValues: {
      ...props.account?.details?.client_details,
    } as AccountClientDetails,
    onSubmit: handleSubmit,
  });

  return (
    <form onSubmit={formik.handleSubmit}>
      <FormGroup heading="Datos del cliente">
        <FormGroup.Item>
          <TextField
            fullWidth
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <BadgeIcon />
                </InputAdornment>
              ),
            }}
            id="first_name"
            name="first_name"
            label="Nombre(s)"
            variant="filled"
            value={formik.values.first_name}
            onChange={formik.handleChange}
            error={
              formik.touched.first_name && Boolean(formik.errors.first_name)
            }
            helperText={formik.touched.first_name && formik.errors.first_name}
          />
        </FormGroup.Item>
        <FormGroup.Item>
          <TextField
            fullWidth
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <BadgeIcon />
                </InputAdornment>
              ),
            }}
            id="last_name"
            name="last_name"
            label="Apellidos"
            variant="filled"
            value={formik.values.last_name}
            onChange={formik.handleChange}
            error={formik.touched.last_name && Boolean(formik.errors.last_name)}
            helperText={formik.touched.last_name && formik.errors.last_name}
          />
        </FormGroup.Item>
        <FormGroup.Item>
          <TextField
            fullWidth
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <BadgeIcon />
                </InputAdornment>
              ),
            }}
            id="rut"
            name="rut"
            label="Rut"
            variant="filled"
            value={formik.values.rut}
            onChange={formik.handleChange}
            error={formik.touched.rut && Boolean(formik.errors.rut)}
            helperText={formik.touched.rut && formik.errors.rut}
          />
        </FormGroup.Item>
        <FormGroup.Item>
          <FormControl variant="filled" fullWidth>
            <InputLabel id="gender_label">Sexo</InputLabel>
            <Select
              startAdornment={
                <InputAdornment position="start">
                  <WcIcon />
                </InputAdornment>
              }
              id="gender"
              labelId="gender_label"
              name="gender"
              label="Sexo"
              value={formik.values.gender}
              defaultValue="GENDER_UNSPECIFIED"
              onChange={formik.handleChange}
              error={formik.touched.gender && Boolean(formik.errors.gender)}
            >
              <MenuItem value="GENDER_UNSPECIFIED">No especificado</MenuItem>
              <MenuItem value="GENDER_MALE">Masculino</MenuItem>
              <MenuItem value="GENDER_FEMALE">Femenino</MenuItem>
            </Select>
          </FormControl>
        </FormGroup.Item>
        <FormGroup.Item>
          <FormControl variant="filled" fullWidth>
            <InputLabel id="marital_status_label">Estado Civil</InputLabel>
            <Select
              fullWidth
              startAdornment={
                <InputAdornment position="start">
                  <FamilyRestroomIcon />
                </InputAdornment>
              }
              id="marital_status"
              labelId="marital_status_label"
              name="marital_status"
              label="Estado Civil"
              value={formik.values.marital_status}
              defaultValue="MARITAL_STATUS_UNSPECIFIED"
              onChange={formik.handleChange}
              error={
                formik.touched.marital_status &&
                Boolean(formik.errors.marital_status)
              }
            >
              <MenuItem value="MARITAL_STATUS_UNSPECIFIED">
                No especificado
              </MenuItem>
              <MenuItem value="MARITAL_STATUS_SINGLE">Soltero(a)</MenuItem>
              <MenuItem value="MARITAL_STATUS_MARRIED">Casado(a)</MenuItem>
              <MenuItem value="MARITAL_STATUS_WIDOWED">Viudo(a)</MenuItem>
              <MenuItem value="MARITAL_STATUS_SEPARATED">Separado(a)</MenuItem>
              <MenuItem value="MARITAL_STATUS_DIVORCED">Divorciado(a)</MenuItem>
            </Select>
          </FormControl>
        </FormGroup.Item>
        <FormGroup.Item>
          <TextField
            fullWidth
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <WorkIcon />
                </InputAdornment>
              ),
            }}
            id="occupation"
            name="occupation"
            label="Ocupacion u oficio"
            variant="filled"
            value={formik.values.occupation}
            onChange={formik.handleChange}
            error={
              formik.touched.occupation && Boolean(formik.errors.occupation)
            }
            helperText={formik.touched.occupation && formik.errors.occupation}
          />
        </FormGroup.Item>
      </FormGroup>
      <FormGroup heading="Direccion particular">
        <FormGroup.Item>
          <TextField
            fullWidth
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <HomeIcon />
                </InputAdornment>
              ),
            }}
            id="street"
            name="street"
            label="Calle"
            variant="filled"
            value={formik.values.street}
            onChange={formik.handleChange}
            error={formik.touched.street && Boolean(formik.errors.street)}
            helperText={formik.touched.street && formik.errors.street}
          />
        </FormGroup.Item>
        <FormGroup.Item>
          <Grid container spacing={2}>
            <Grid item xs={6}>
              <TextField
                fullWidth
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <NumbersIcon />
                    </InputAdornment>
                  ),
                }}
                id="street_number"
                name="street_number"
                label="Numero"
                variant="filled"
                value={formik.values.street_number}
                onChange={formik.handleChange}
                error={
                  formik.touched.street_number &&
                  Boolean(formik.errors.street_number)
                }
                helperText={
                  formik.touched.street_number && formik.errors.street_number
                }
              />
            </Grid>
            <Grid item xs={6}>
              <TextField
                fullWidth
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <ApartmentIcon />
                    </InputAdornment>
                  ),
                }}
                id="apartment_number"
                name="apartment_number"
                label="Apartamento (opcional)"
                variant="filled"
                value={formik.values.apartment_number}
                onChange={formik.handleChange}
                error={
                  formik.touched.apartment_number &&
                  Boolean(formik.errors.apartment_number)
                }
                helperText={
                  formik.touched.apartment_number &&
                  formik.errors.apartment_number
                }
              />
            </Grid>
          </Grid>
        </FormGroup.Item>
        <FormGroup.Item>
          <FormControl variant="filled" fullWidth>
            <InputLabel id="region_label">Regin</InputLabel>
            <Select
              fullWidth
              startAdornment={
                <InputAdornment position="start">
                  <SouthAmericaIcon />
                </InputAdornment>
              }
              id="region"
              labelId="region_label"
              name="region"
              label="Regin"
              value={formik.values.region}
              onChange={formik.handleChange}
              error={formik.touched.region && Boolean(formik.errors.region)}
            >
              {Object.keys(regionCommune).map((region) => (
                <MenuItem value={region} key={region}>
                  {region}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </FormGroup.Item>
        <FormGroup.Item>
          <FormControl variant="filled" fullWidth>
            <InputLabel id="commune_label">Comuna</InputLabel>
            <Select
              fullWidth
              startAdornment={
                <InputAdornment position="start">
                  <PlaceIcon />
                </InputAdornment>
              }
              id="commune"
              labelId="commune_label"
              name="commune"
              label="Comuna"
              value={formik.values.commune}
              onChange={formik.handleChange}
              error={formik.touched.commune && Boolean(formik.errors.commune)}
            >
              {regionCommune[formik.values.region || "No especificada"].map(
                (commune) => (
                  <MenuItem value={commune} key={commune}>
                    {commune}
                  </MenuItem>
                )
              )}
            </Select>
          </FormControl>
        </FormGroup.Item>
      </FormGroup>
      <FormGroup heading="Contacto">
        <FormGroup.Item>
          <TextField
            fullWidth
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <PhoneIcon />
                </InputAdornment>
              ),
            }}
            id="phone_number"
            name="phone_number"
            label="Telefono"
            variant="filled"
            value={formik.values.phone_number}
            onChange={formik.handleChange}
            error={
              formik.touched.phone_number && Boolean(formik.errors.phone_number)
            }
            helperText={
              formik.touched.phone_number && formik.errors.phone_number
            }
          />
        </FormGroup.Item>

        <FormGroup.Item>
          <FormControl variant="filled" fullWidth>
            <InputLabel id="isapres_label">Isapre Actual</InputLabel>
            <Select
              fullWidth
              startAdornment={
                <InputAdornment position="start">
                  <HealthAndSafetyIcon />
                </InputAdornment>
              }
              id="isapres"
              labelId="isapres_label"
              name="isapres"
              label="Isapre Actual"
              value={formik.values.isapres}
              defaultValue="No especificado"
              onChange={formik.handleChange}
              error={formik.touched.isapres && Boolean(formik.errors.isapres)}
            >
              <MenuItem value="No especificado">No especificado</MenuItem>
              <MenuItem value="Banmdica S.A.">Banmdica S.A.</MenuItem>
              <MenuItem value="Colmena Golden Cross S.A.">
                Colmena Golden Cross S.A.
              </MenuItem>
              <MenuItem value="Consalud S.A.">Consalud S.A.</MenuItem>
              <MenuItem value="Cruz Blanca S.A.">Cruz Blanca S.A.</MenuItem>
              <MenuItem value="Nueva Masvida S.A. (Ex ISAPRE Masvida S.A.)">
                Nueva Masvida S.A. (Ex ISAPRE Masvida S.A.)
              </MenuItem>
              <MenuItem value="Vida Tres S.A.">Vida Tres S.A.</MenuItem>
            </Select>
          </FormControl>
        </FormGroup.Item>
      </FormGroup>

      <FormControlLabel
        required
        control={<Checkbox />}
        label={
          <Typography variant="body2">
            Aseguro que entre los beneficiarios de mi plan de salud hay al menos
            un(a) menor que cumpli 2 aos de edad entre el 2011 y 2020 y/o una
            mujer, sea cotizante o carga, que haya cumplido 40 aos en el mismo
            perodo
          </Typography>
        }
      />
      <FormControlLabel
        required
        control={<Checkbox />}
        label={
          <Typography variant="body2">
            He ledo y acepto los
            <a
              href="/assets/documents/CONTRATO_AUDITORA_Y_RECUPERACION_EXCESOS_PROCESS_EDGE.pdf"
              target="_blank"
            >
              <Typography
                variant="inherit"
                color="secondary"
                display="inline"
                marginLeft={1}
              >
                trminos y condiciones.
              </Typography>
            </a>
          </Typography>
        }
      />
      <br />
      <br />
      <Grid container justifyContent="center">
        <LoadingButton
          color="primary"
          variant="contained"
          type="submit"
          endIcon={<Send />}
          loading={isLoading}
        >
          Aceptar
        </LoadingButton>
      </Grid>
    </form>
  );
}
