import React, { Component, PropsWithChildren } from "react";
import { Box, Card, Grid, Typography } from "@mui/material";

interface SectionItemProps extends PropsWithChildren {
  label: string;
}
export class Section extends Component<PropsWithChildren> {
  static Item(props: SectionItemProps) {
    return (
      <>
        <Grid container>
          <Grid item xs={4}>
            <Typography variant="caption">{props.label}</Typography>
          </Grid>
          <Grid item xs>
            <Typography variant="subtitle2">{props.children}</Typography>
          </Grid>
        </Grid>
      </>
    );
  }

  render() {
    return (
      <>
        <Card>
          <Box padding={3} width={450}>
            {this.props.children}
          </Box>
        </Card>
      </>
    );
  }
}
